@import url(https://fonts.googleapis.com/css?family=Gelasio:700|Open+Sans:400,600&display=swap);
html,body{max-width:720px;color:#02241F;padding:0px 20px;margin:0px auto;box-sizing:border-box;outline:0;height:100%;font-family:"Open Sans",sans-serif;font-size:16px}h1,h2,h3,h4,h5,h6{font-family:"Gelasio",serif}ul,li{padding:0px;margin:0px}a{text-decoration:none;color:#02241F;-webkit-transition:all .3s ease-in-out;transition:all .3s ease-in-out}a:hover{color:#FF6060;border-radius:4px;border-bottom:4px solid #FF6060}.container{margin:30px auto 60px}.navbar{display:flex;align-items:baseline;justify-content:space-between;padding:14px 0px;border-bottom:1px solid #FFD853}.navbar_left h1{margin:0px}.navbar_left a{font-size:26px;font-family:"Gelasio",serif}@media (min-width: 576px){.navbar_left a{font-size:36px}}.navbar_left a:hover{border-radius:none;border-bottom:none}.navbar_right a{font-size:12px;margin-left:8px;margin-right:8px;font-weight:600;line-height:0.7;letter-spacing:.1em;opacity:1;text-transform:uppercase}@media (min-width: 576px){.navbar_right a{font-size:14px}}.home div:first-of-type{margin-bottom:20px}@media (min-width: 576px){.home div:first-of-type{margin-bottom:40px}}.home a{color:#FF6060}.home_photos h2 span{color:#559AFF;font-size:12px;font-weight:400}.home_photos img{width:100px;height:100px;border:1px solid #02241F;border-radius:8px;margin-right:5px}.now{padding-bottom:50px}.now_section h1{font-size:24px}.now_section h3{font-size:20px}.now_section a{color:#FF6060}.now_section a:hover{border-radius:4px;border-bottom:4px solid #FF6060}.now_section:last-of-type{margin-top:40px}.now_section.reads{display:block}@media (min-width: 576px){.now_section.reads{display:flex;flex:1 1}}@media (min-width: 576px){.now_section_reads{flex:1 1}}.now_section_reads ul li{list-style-type:none}.now_section:last-of-type{font-size:14px}

